<template>

  <el-container>
    <el-header>
      <el-row :gutter="20">

        <el-col :span="6">
          <wedit v-model="key0" ref="keyfld" input-hint="请输入key"></wedit>
        </el-col>
        <el-col :span="6">
          <wselect v-model="keyType" input-hint="请选择key类型" list-name="icbcKeyType"></wselect>
        </el-col>

        <el-col :span="6">
          <mselect v-model="models" split-word="|" input-hint="请选择model类型" list-name="icbcInfoModel"></mselect>
        </el-col>


        <el-col :span="6">

          <wbutton  task-name="icbcInfo" :task-para="getInfoTaskPara" @taskFinish="taskFinish">情报查询</wbutton>

        </el-col>

      </el-row>

    </el-header>

    <el-container>
      <div style="height:500px;width:100%;overflow:auto;">

        <el-scrollbar>
          <div v-for="(infoitem,index) in getIcbcInfo" :key="index">

            <info-show  :infoData="infoitem"></info-show>
          </div>
        </el-scrollbar>
      </div>
    </el-container>

  </el-container>


</template>

<script>
  import wselect from "../wcontrol/wselect";
  import mselect from "../wcontrol/mselect";
  import wedit from "../wcontrol/wedit";
  import wbutton from "../wcontrol/wbutton";
  import infoShow from "./infoShow";


  export default {
    name: "info",
    components: {wselect,mselect, wedit, wbutton, infoShow},
    data() {
      return {
        keyType: "32",
        key0: "小米科技有限责任公司",
        models: "",
        taskName:"icbcInfo",
        infodata: null,
        infoinfo: null,
      }
    },
    created() {

    },
    computed:
      {
        getInfoTaskPara() {
          console.debug("infoTaskPara")
          // //this.models = "SHAREHOLDER|PERSON|YEARREPORTPAIDUPCAPITAL"
          // this.models = "PERSON"
          let para = {taskName: "icbcInfo", key: this.key0, model: this.models, keyType: this.keyType};
          console.debug(para)
          return para;
        },
        getIcbcInfo() {
          if (this.infodata) {
            console.debug("icbc info:",this.infodata.items[0].info);
            return this.infodata.items[0].info;
          }
        }
      },
    methods: {

      taskFinish(data)
      {
        console.debug("taskfinish")
        this.infodata = data;
      }
    }

  }
</script>

<style scoped>


</style>



