<template>
  <div>

    <el-row :gutter="20">

      <el-col :span="4">

      </el-col>

      <el-col :span="8">
        {{resultDesc}}
      </el-col>

    </el-row>
    <div v-if="checkShow()">
      <el-table
        :data="getinfoDataDetail()"
        border
        style="width: 100%"
        height="300">

        <el-table-column :show-overflow-tooltip="true" v-for="(value,key0) in icbcInfo.INFO " :key="key0"
                         :prop="key0"
                         :label="value"
        >
        </el-table-column>

      </el-table>

      <el-pagination v-if="checkPageShow()"
                     layout="prev, pager, next"
                     :total="infoData.TOTALCOUNT">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import store from '@/store/index'

  export default {
    components:{store},
    name: "infoShow",
    props:
      {
        infoData: null,
      },
    data() {
      return {
        icbcInfo: {},
        resultDesc: "",
        hasDone: false,

      }

    },
    created()
    {
      this.getinfoHeader();
    },
    computed:
      {
        getModel() {
          if (this.infoData) {
            return this.infoData.MODEL;
          }
        }
      },
    methods:
      {
        getinfoHeader()
        {
          let header =   this.$store.getters["icbc/getInfoHeader"](this.getModel);
          if(header!=null)
          {

            this.icbcInfo = header;
            console.debug("get head info : "+ this.getModel, this.icbcInfo)
            return header;
          }

            let info = {model: this.getModel, infoType: "infoIcbcModel"}
            console.debug(info)
            store.dispatch("icbc/getIcbcInfoHeadAct", info).then((data) => {

              this.icbcInfo = data;
              console.debug("get icbc info data", data)

              return data;
            });



        },


        getinfoDataDetail() {
          console.debug(this.infoData);
          if (this.infoData && this.infoData.TDATA && this.infoData.TDATA.length > 0) {
            this.resultDesc = this.infoData.MODEL;
            return this.infoData.TDATA;
          }

        },
        checkShow() {
          console.debug("checkShow()")
          if (this.infoData == null) return false;
          if (this.infoData.TDATA == null || this.infoData.TDATA.length == 0) return false;
          // if (this.infoData.TCONF == null || (Object.getOwnPropertyNames(this.infoData.TCONF)).length<=2) return false;
          this.getinfoHeader();

          return true;

        },
        checkPageShow() {
          console.debug("checkPageShow", this.infoData)

          if (this.infoData && this.infoData.TDATA.length > 0) return true;
          else return false;

        },
      },
  }
</script>

<style scoped>

</style>



