<template>
  <div>

    <el-row :gutter="20">

      <el-col :span="4">
        {{riskData.RISKTYPE}}
      </el-col>
      <el-col :span="8">
        {{riskData.RISKTYPEDES}}
      </el-col>
      <el-col :span="2">
        {{riskData.MODEL}}
      </el-col>
      <el-col :span="8">
        {{getRiskDataCountDesc()}}
      </el-col>

    </el-row>
    <div v-if="checkShow()">
      <el-table
        :data="getRiskDataDetail()"
        style="width: 100%"
        height="300">

        <el-table-column :show-overflow-tooltip="true" v-for="(value,key0) in getRiskHeader()" :key="key0"
                         :prop="key0"
                         :label="value"
        >
        </el-table-column>

      </el-table>

      <el-pagination v-if="checkPageShow()"
                     layout="prev, pager, next"
                     :total="riskData.TOTALCOUN">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {

    name: "riskShow",
    props:
      {
        riskData: null,
      },
    data() {
      return {}

    },
    computed:
      {},
    methods:
      {
        getRiskHeader() {

          console.debug(this.riskData)

          if (this.riskData) {
            return this.riskData.TCONF;
          }
        },
        getRiskDataDetail() {
          console.debug(this.riskData);
          if (this.riskData && this.riskData.TDATA.length > 0) {

            return this.riskData.TDATA;
          }

        },
        checkShow() {
          if (this.riskData == null) return false;
          if (this.riskData.TDATA == null || this.riskData.TDATA.length == 0) return false;
          if (this.riskData.TCONF == null || (Object.getOwnPropertyNames(this.riskData.TCONF)).length<=2) return false;
          return true;

        },
        checkPageShow() {

          if (this.riskData && this.riskData.TDATA.length > 5) return true;
          else return false;

        },
        getRiskDataCountDesc() {
          if (this.checkShow() == false) {
            return "共" + this.riskData.TOTALCOUNT + "条数据，没有详细资料"
          } else
            return "共" + this.riskData.TOTALCOUNT + "条数据，详细信息如下："
        }
      }
  }
</script>

<style scoped>

</style>



