import store from "./index";
import axios from '@/utils/axios.js'

const state = {
  relationNodes: [],
  relationEdges: [],
  existData:[],
  itemShow: [],
  currentLevel: 2,
  changeNodeCount: 1,
  itemSelect: [3, 0, 0, 0],
  idbcInfoHead: {},
  //totalLevel:0,
};

const mutations = {
  setRelationCommit: (state, data) => {
    let key = data.key;
    if(state.existData[key]) return;
    if (data.nodes.length > 0) {
      state.relationNodes = state.relationNodes.concat(data.nodes);
      state.relationEdges = state.relationEdges.concat(data.edges);

      state.existData[key]=true;

    }
  },
  setSelectNode: (state, data) => {
    if (state.itemSelect[data.level] == data.no) return;
    state.itemSelect[data.level] = data.no;
    state.currentLevel = data.level;
    state.changeNodeCount++;
  },

  setIcbcInfoHeadCommit: (state, data) => {
    console.debug("data hhhhhhh");
    state.idbcInfoHead = data;
  }
};

const getters = {
  getShowFlag: (state) => (level, no) => {

    if (level == 0) return true;
    if (level > state.currentLevel) return false;
    if (state.itemSelect[level] == null) return true;
    if (state.itemSelect[level] == parseInt(no)) return true;
    return false;
  },
  getInfoHeader: (state) => (model) => {

    return state.idbcInfoHead[model];

  },
  getChild: (state) => (parentId, flag) => {
    let retList = [];
    let i = 0;
    console.debug(parentId, flag)
    for (i = 0; i < state.relationEdges.length; i++) {

      if (flag == "source") {
        if (parentId == state.relationEdges[i].source) {
          //         console.debug(state.relationEdges[i].source)
          //        console.debug(state.relationEdges[i].target)
          retList.push(state.relationEdges[i].target)
        }
      } else if (flag == "target") {
        if (parentId == state.relationEdges[i].target) {
          retList.push(state.relationEdges[i].source)
        }
      }
    }
    console.debug(retList)
    return retList;
  },
  getNode: (state) => (key) => {
    let i = 0;
    for (i = 0; i < state.relationNodes.length; i++) {
      if (key == state.relationNodes[i].id) return state.relationNodes[i];
      if (key == state.relationNodes[i].name) return state.relationNodes[i];
    }
    return null;
  },
};

const actions = {


  getIcbcInfoHeadAct: (context, info) => {
    console.debug("actions:" + info.model);
    //let reqData ={taskType:"getlist", listName:listname}


    return new Promise((resolve, reject) => {
      if (context.state.idbcInfoHead[info.model] != null) {
        resolve(context.state.idbcInfoHead[info.model]);
        return;
      }
      axios.getInfo(info).then(({data}) => {
        if (data.isSuccess) {

          context.commit("setIcbcInfoHeadCommit", data.info);

          if (context.state.idbcInfoHead[info.model] != null) {
            let info0 = context.state.idbcInfoHead[info.model].INFO;

            resolve(info0);
            return;
          }


        } else {
          console.log("getIcbc info error：" + info.model);
          //reject(data);
        }
      })
    })
  },


};


export default {
  namespaced: true,   //namespaced: true 的方式使其成为带命名空间的模块。保证在变量名一样的时候，添加一个父级名拼接。
  state,
  getters,
  actions,
  mutations
}



