<template>

  <el-container>
    <el-header>
      <el-row :gutter="20">

        <el-col :span="6">
          <wedit v-model="key0" ref="keyfld" input-hint="请输入key"></wedit>
        </el-col>
        <el-col :span="6">
          <wselect v-model="keyType" input-hint="请选择key类型" list-name="icbcKeyType"></wselect>
        </el-col>

        <el-col :span="6">
          <mselect v-model="models"  split-word="|" input-hint="请选择model类型"  list-name="icbcRiskModel"></mselect>
        </el-col>


        <el-col :span="6">

          <wbutton  task-name="icbcRisk" :task-para="getRiskTaskPara" @taskFinish="taskFinish">风险查询</wbutton>

        </el-col>

      </el-row>

    </el-header>
    <el-container>
      <div style="height:500px;width:100%;overflow:auto;">

        <el-scrollbar>
          <div v-for="(riskitem,index) in getRiskInfo" :key="index">

            <risk-show  :riskData="riskitem"></risk-show>
          </div>
        </el-scrollbar>
      </div>
    </el-container>

  </el-container>


</template>

<script>
  import wselect from "../wcontrol/wselect";
  import mselect from "../wcontrol/mselect";
  import wedit from "../wcontrol/wedit";
  import wbutton from "../wcontrol/wbutton";
  import riskShow from "./riskShow";


  export default {
    name: "risk",
    components: {wselect, mselect,wedit, wbutton, riskShow},
    data() {
      return {
        keyType: null,
        key0: null,
        models: null,
        riskdata: null,
        riskinfo: null,
      }
    },
    created() {

    },
    computed:
      {

        getRiskInfo() {
          if (this.riskdata) {
            console.debug("risk info:",this.riskdata.items[0].info);
            return this.riskdata.items[0].info;
          }
        }
      },
    methods: {
          taskFinish(data)
          {
            this.riskdata = data;
          },
      getRiskTaskPara() {
        console.debug("riskTaskPara")

        let para = {taskName: "icbcRisk", key: this.key0, model: this.models, keyType: this.keyType};
        console.debug(para)
        return para;
      },
    }

  }
</script>

<style scoped>


</style>



